/* Setting Defaults */
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #efefef;
  background-image: url("data:image/svg+xml,%3Csvg width='64' height='64' viewBox='0 0 64 64' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 16c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8zm0-2c3.314 0 6-2.686 6-6s-2.686-6-6-6-6 2.686-6 6 2.686 6 6 6zm33.414-6l5.95-5.95L45.95.636 40 6.586 34.05.636 32.636 2.05 38.586 8l-5.95 5.95 1.414 1.414L40 9.414l5.95 5.95 1.414-1.414L41.414 8zM40 48c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8zm0-2c3.314 0 6-2.686 6-6s-2.686-6-6-6-6 2.686-6 6 2.686 6 6 6zM9.414 40l5.95-5.95-1.414-1.414L8 38.586l-5.95-5.95L.636 34.05 6.586 40l-5.95 5.95 1.414 1.414L8 41.414l5.95 5.95 1.414-1.414L9.414 40z' fill='%23deddde' fill-opacity='0.25' fill-rule='evenodd'/%3E%3C/svg%3E");
}

@media (min-width: 1366.1px) {
  body {
    margin: 25px;
  }
  #root {
    border-radius: 5px;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

button {
  border: none;
  background-color: transparent;
}

button:focus {
  outline: none;
}

input {
  outline-color: #ccc;
}

#root {
  overflow: hidden;
  box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.06);
  background-color: #f7f8f9;
}

#root:empty {
  box-shadow: none;
}

/* Custom Global CSS */
.modal-open {
  overflow: hidden;
}

.w2-5 {
  width: 2.5rem;
}

.w3-5 {
  width: 6rem;
}

.shadow-6 {
  box-shadow: 2px 2px 8px 0 rgba(0, 0, 0, 0.1);
}

.shadow-7 {
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.06);
}

.twitter-picker {
  box-shadow: none !important;
}

.left30 {
  left: 30%;
}

.bg-black-03 {
  background-color: rgba(0, 0, 0, 0.03);
}

.bg-black-07 {
  background-color: rgba(0, 0, 0, 0.07);
}

.badge {
  display: inline-block;
  padding: 0.25em 0.4em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25rem;
  color: white;
}

.btn-visible-focus:focus {
  opacity: 100;
  box-shadow: 0 0 1px 3px rgba(109, 109, 249, 0.38);
}

.noselect {
  -ms-user-select: none;
  user-select: none;
}
